import * as React from 'react';
import { IconButton } from './Basics';
import type { IIconButtonProps } from './Basics';
import { IconSuperscript, IconSubscript } from '../icons';

export function SuperscriptButton(props: IIconButtonProps) {
  const {
    icon = <IconSuperscript />,
    ...rest
  } = props;

  return (
    <IconButton
      testid="SuperscriptButton"
      icon={icon}
      {...rest}
    />
  )
}

export function SubscriptButton(props: IIconButtonProps) {
  const {
    icon = <IconSubscript />,
    ...rest
  } = props;

  return (
    <IconButton
      testid="SubscriptButton"
      icon={icon}
      {...rest}
    />
  )
}

